import React from 'react'

function ThemesCard({themesData}) {
    return (
        <>
            <section className="mainBg">
                {themesData.map((curElem) =>{
                    return(
                        <>
                            <div className="card" key={curElem.id}>
                                <a href={curElem.url} target="_blank"><img src={curElem.img} alt={curElem.category} /></a>
                                <br />
                                <h4>{curElem.category}</h4>
                                <br />
                                <h3 className="code">Code: {curElem.code}</h3>
                            </div>
                        </>
                    )
                })}
            </section>
        </>
    )
}

export default ThemesCard
