import React, {useState} from 'react';
import ThemesCard from './ThemesCard';
import themes from './ThemesDatabse';

function Themes() {
    const [themesData, setThemesData] = useState(themes);
    const filterThemes = (category) => {
        const updatedThemes = themes.filter((curElem) => {
            return curElem.category === category;
        })
        setThemesData(updatedThemes);
    }

    const MenuList = [...new Set(themes.map((curElem) =>{
        return curElem.category
    }))]

    return (
        <>
            <div className="filters">
                <p>Filter through category: </p>
                <div className="btn-grp">
                    <button onClick={()=> filterThemes("Electronics")}>Electronics</button>
                    <button onClick={()=> filterThemes("Fashion")}>Fashion</button>
                    <button onClick={()=> filterThemes("Grocery")}>Grocery</button>
                    <button onClick={()=> filterThemes("Multipurpose")}>Multipurpose</button>
                    <button onClick={()=> setThemesData(themes)}>All</button>
                </div>
            </div>
            <ThemesCard themesData={themesData} />
        </>
    )
}

export default Themes
